.pad {
  padding: 1rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  border-radius: 14px;
  border-color: rgb(140, 0, 255);
  border-width: 3px;
  background-color: white;
  max-width: 45rem;
  margin: auto;
  position: relative;
  left: 16%;
  margin-right: 10px;
  margin-top: 10px;
}
